/* similuated page */
.container {
    max-width: 1000px;
    margin: 30px auto;
    font-family: Verdana, Arial, sans-serif;
    font-size: 16px;
    line-height: 1.4;
    color: #222;
}

$red: #FF7676;
$transparentred: rgba(255, 118, 118, .65);
$blue: #466C95;
$transparentblue: rgba(70, 108, 149, .65);
$green: #5DAE8B;
$transparentgreen: rgba(93, 174, 139, .65);
$yellow: #F6C90E;
$transparentyellow: rgba(246, 201, 14, .65);
$downArrow: "data:image/png;base64,";
@mixin reset {
    margin: 0;
    padding: 0;
}


/* fullscreen */
.container--fullscreen {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    max-width: 100%;
    margin: 0;

    #devtrac--mapbox {
        height: 100%;
        width: 100%;
    }
}


/* interactive wrapper */
#devtrac {
    position: relative;
    overflow: hidden;
}

/* map container */
#devtrac--mapbox {
    height: 600px;
    width: 1000px;
}

/* map overrides */
.mapboxgl-ctrl.mapboxgl-ctrl-attrib {
    margin-right: -105px !important;
    transition: margin .4s;
}
.mapboxgl-ctrl.mapboxgl-ctrl-attrib:hover {
    margin-right: 0 !important;
}
.mapboxgl-ctrl-compass {
    display: none !important;
}
.mapboxgl-ctrl-icon.mapboxgl-ctrl-zoom-out {
    border-bottom: 0;
}

/* utils */
.animated {
    transition: .2s ease-in-out;
}
.pointer:hover {
    cursor: pointer;
}

/* legend */
.devtrac--legend {
    position: absolute;
    top: 10px;
    right: 50px;
    padding: 5px;
    border-radius: 4px;
    text-align: right;
    color: #444;
    box-shadow: rgba(0, 0, 0, 0.05) 4px 4px 10px 0px;
    background: rgba(255,255,255,0.6);

    .legend--status {
        display: inline-block;
        margin: 2px 5px;
        font-size: 12px;
        line-height: 14px;
        vertical-align: middle;
    }
    @media screen and (max-width: 875px) {
        .legend--status {
            float: left;
        }
    }
    .legend--circle {
        display: inline-block;
        position: relative;
        top: -2px;
        height: 14px;
        width: 14px;
        margin-right: 5px;
        border-radius: 7px;
        vertical-align: middle;
    }
    .status--proposed .legend--circle {
        background: $red;
    }
    .status--approved .legend--circle {
        background: $yellow;
    }
    .status--underConstruction .legend--circle {
        background: $blue;
    }
    .status--contructionCompleted .legend--circle {
        background: $green;
    }

}
@media screen and (max-width: 875px) {
    .devtrac--legend {
        width: 180px;
    }
}
.devtrac--legend:hover {
    background: rgba(255,255,255,1);
}

.devtrac--fullscreen {
    position: absolute;
    top: 80px;
    right: 10px;
    height: 20px;
    width: 20px;
    font-size: 16px;
    line-height: 20px;
    padding: 7px 2px 2px 8px;
    border-radius: 4px;
    background: #fff;
    box-shadow: 0px 0px 0px 2px rgba(0,0,0,0.1);
}

/* filters */
.devtrac--filterbox {
    position: absolute;
    top: 10px;
    left: 10px;
    z-index: 1;
    width: 290px;
    max-height: 400px;
    border-radius: 4px;
    box-shadow: rgba(0, 0, 0, 0.1) 4px 4px 10px 0px;
    background: rgba(255,255,255,1);
    overflow: hidden;

    .filterbox--inner {
        margin: 10px;
    }
    .filterbox--title {
        margin: 0 0 10px 0;
        padding: 0 0 10px 0;
        font-size: 16px;
        border-bottom: 1px solid #e5e5e5;
    }
    .fa-caret-down {
        padding: 0;
        margin: 2px 0 0 0;
        border: 0;
        float: right;
        line-height: 16px;
        font-size: 24px;
    }

    .filterbox--inputwrapper {
        margin-bottom: 15px;
        font-size: 14px;

        .filterbox--header {
            margin: 0 0 5px 0;
            padding: 0 0 5px 0;
            font-size: 14px;
            border-bottom: 1px solid #e5e5e5;
        }

        .inputwrapper--twocol {
            display: inline-block;
        }
        .inputwrapper--twocol:last-of-type {
            float: right;
        }

        select {
            width: 100%;
            margin: 0;
            padding: 5px 10px;
            outline: none;
            border: 1px solid #e5e5e5;
            border-radius: 0;
            -webkit-appearance: none;
            background: #fff url($downArrow) no-repeat right center;
            background-size: contain;
            font-size: 14px;
            line-height: 16px;
        }

        input[type="text"] {
            width: 100%;
            padding: 5px 7px;
            border: 1px solid #e5e5e5;
            border-radius: 0;
            font-size: 14px;
            line-height: 16px;
        }

        /* custom checkboxes */
        input[type=checkbox] {
            position: relative;
            top: 3px;
            margin: 2px 0 0 0;
            padding: 0;
            outline: none;
            font-size: 20px;
            -webkit-appearance:none;
            -moz-appearance:none;
        }
        /* unchecked icon */
        input[type=checkbox]:before {
            display: inline-block;
            margin-right: 3px;
            font-family: FontAwesome;
            content: "\f096";
            color: #555;
        }
        /* checked icon */
        input[type=checkbox]:checked:before {
            content: "\f046";
            margin-right: 0;
        }

    }

    .filters--status {

        input[type=checkbox].proposed:before {
            color: $red;
        }
        input[type=checkbox].approved:before {
            color: $yellow;
        }
        input[type=checkbox].under:before {
            color: $blue;
        }
        input[type=checkbox].completed:before {
            color: $green;
        }
    }

}
.devtrac--filterbox:hover {
    background: rgba(255,255,255,1)
}
.filterbox--hidden {
    max-height: 42px;
    background: rgba(255,255,255,0.6);

    .fa-caret-down {
        transform: rotate(90deg);
    }
}


/* infobox */
.devtrac--infobox {
    position: absolute;
    bottom: 10px;
    left: 10px;
    z-index: 1;
    right: 10px;
    height: 170px;
    border-radius: 4px;
    box-shadow: rgba(0, 0, 0, 0.1) 4px 4px 10px 0px;
    background: rgba(255,255,255,1);

    .infobox--outer {
        width: 100%;
        height: 100%;
        overflow: hidden;
    }

    .infobox--inner {
        margin: 10px;

        .infobox--imagewrap {
            position: relative;
            float: left;
            text-transform: capitalize;

            .imagewrap--status {
                position: absolute;
                bottom: -10px;
                left: -10px;
                width: 100%;
                padding: 2px 0px;
                text-align: center;
                font-size: 14px;
                color: #fff;
            }

            .status--proposed {
                background: $transparentred;
            }
            .status--approved {
                background: $transparentyellow;
            }
            .status--under-construction {
                background: $transparentblue;
            }
            .status--construction-completed {
                background: $transparentgreen;
            }

            .imagewrap--image {
                display: block;
                height: 170px;
                margin: -10px 10px -10px -10px;
                width: auto;
                float: left;
            }
        }

        .infobox--details {

            .details--name {
                @include reset;
                font-size: 14px;
            }
            .details--estimates {
                @include reset;
                float: right;
                font-size: 12px;
                line-height: 16px;
                text-align: right;
            }
            .details--location {
                @include reset;
                font-size: 12px;
                font-style: italic;
            }
            .details--desc {
                margin: 5px 0;
                padding: 0;
                font-size: 12px;

            }
            .details--who {
                @include reset;
                font-size: 12px;
            }
            .details--developer {
                margin-right: 30px;
            }
            .details--coverage {
                @include reset;
                font-size: 12px;
                margin-top: 5px;

                .coverage--bullet {
                    font-size: 14px;
                    line-height: 12px;
                    margin: 0 2px 0 4px;
                }

                .coverage--link {
                    color: #002269;
                    text-decoration: none;
                }

                .coverage--link:hover {
                    text-decoration: underline;
                }

            }

        }

    }

    .fa-times-circle {
        position: absolute;
        top: -9px;
        right: -9px;
        font-size: 30px;
        color: #222;
        background: #fff;
        border-radius: 15px;
        line-height: 15px;
        z-index: 2;
    }

}
.infobox--hidden {
    transform: translateY(200px);
}

.devtrac--touch {
    display: none;
}

@media screen and (max-width: 620px) {

    #devtrac--mapbox {
        height: 300px;
        width: 100%;
    }

    .devtrac--legend {
        display: none;
    }

    .devtrac--filterbox {
        width: 236px;
        overflow-y: auto;
        -webkit-overflow-scrolling: touch;

        .filterbox--inputwrapper {
            font-size: 12px;
        }

    }

    .devtrac--infobox {
        height: initial;
        max-height: 96%;
        width: 94%;
        top: 3%;
        bottom: initial;
        left: 3%;
        right: 3%;
        -webkit-overflow-scrolling: touch;

        .infobox--outer {
            overflow-y: auto;
        }

        .infobox--inner {
            .infobox--imagewrap {
                float: none;
                margin: -10px -10px 10px -10px;

                .imagewrap--image {
                    width: 100%;
                    height: auto;
                    float: none;
                    margin: 0;
                }
                .imagewrap--status {
                    bottom: 0;
                    left: 0;
                }
            }
            .infobox--details {
                .details--developer {
                    display: block;
                    margin-right: 0;
                }
                .details--architect {
                    display: block;
                }
            }
        }

    }
    .infobox--hidden {
        transform: translateY(200%);
        bottom: 0;
    }

    .devtrac--touch {
        display: block;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgba(55,55,55,.5);
        z-index: 2;

        .touch--wrapper {
            position: absolute;
            top: 50%;
            left: 0;
            text-align: center;
            right: 0;
            color: #fff;
            text-transform: uppercase;
            letter-spacing: 1px;
            margin-top: -40px;

            .fa {
                font-size: 50px;
                transform: rotate(-35deg);
                line-height: 50px;
                margin-bottom: 15px;
            }
        }

    }
}



